	methods: {
		init() {
			this.api.home.article.get_detail({
				article_id: id,
				request_type: "app"
			}, res => {
				console.log(res.data)
				this.info = res.data

			})
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true,
					rect: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		goFamous() {
			uni.navigateTo({
